<template lang="html">
  <div class="">
    <default-head header="筛选">
      <span slot="head-right" class="circle-reset">重置</span>
    </default-head>
    <div class="" id="wrapper">
      <div class="select-codition-box">
        <div class="hd-box">
          <span class="title">筛选条件</span>
          <input class="btn-search" type="button" name="" value="搜索">
        </div>
        <form-item-line>
          <i class="search__icon select-codition-icon"></i>
          <input class="select-codition-input" type="text" name="" value="">
        </form-item-line>
      </div>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import formItemLine from '@/components/common/formItemLine'
export default {
  components: {defaultHead, formItemLine}
}
</script>

<style lang="less">
@import '../assets/css/variable.less';
.circle-reset {
  color:@activeColor;
  font-size: .32rem;
}
.select-codition-box {
  background: @whiteColor;
  border: 1px solid @lineColor;
  padding: .2rem .22rem;
  font-size: .32rem;
  color: @defaultColor;
  .hd-box {
    overflow: hidden;
    line-height: .68rem;
    .btn-search {
      border: none;
      width: 1.2rem;
      height: .68rem;
      text-align: center;
      color: @whiteColor;
      background: @greenBtn;
      float: right;
      border-radius: .05rem;
    }
  }
  .select-codition-input {
    width: 100%;
    height: .68rem;
    border-radius: .05rem;
    border: 1px solid @lineColor;
  }
  .select-codition-icon {
    top: 50%;
    transform: translateY(-50%);
    left: .1rem;
  }
}
</style>



